.rh-buttongroup {
    /* Box-modell/beteende */
    display: flex;
    justify-content: center;

    /* Typografi */
    text-align: center;

    @include medium {
        justify-content: left;
    }
}

.rh-buttongroup__button {
    /* Box-modell/beteende */
    min-width: 6em;

    /* Visuellt */
    background-color: #E4E4E4;
    border-bottom: 4px solid $grey-dark;
    padding:2ex;

    &--left {
        /* Visuellt */
        border-top-left-radius: 0.5em;
        border-bottom-left-radius: 0.5em;
        border-right: 1px solid $grey-base;
    }

    &--right {
        /* Visuellt */
        border-top-right-radius: 0.5em;
        border-bottom-right-radius: 0.5em;
        border-left: 1px solid $grey-base;
    }

    &--active,&:focus,&:active,&:hover {
        /* Visuellt */
        background-color: #F4F4F4;
        border-bottom: 4px solid $complementary2;
    }
}

.rh-buttongroup__button .rh-labels {
    /* Box-modell/beteende */
    display: none;

    @include medium {
        /* Box-modell/beteende */
        display: inline;
    }
}

.rh-buttongroup__button-text {
    /* Typografi */
    color: black;
    text-decoration: none;
}