/* ************************* */
/* Atoms - Form control text */
/* ************************* */

// Background
$button--background-color: $vg-color-light-3; // Can using "false"

// Padding
$button--padding-top: 0.625em;
$button--padding-right: 0.375em;
$button--padding-bottom: 0.625em;
$button--padding-left: 0.375em;
$button--padding-top-medium: 0.5em;
$button--padding-right-medium: 0.5em;
$button--padding-bottom-medium: 0.5em;
$button--padding-left-medium: 0.5em;
$button--padding-top-large: 0.625em;
$button--padding-right-large: 0.625em;
$button--padding-bottom-large: 0.625em;
$button--padding-left-large: 0.625em;

// Border
$button--border: 0.0625em solid $grey-base;
$button--border-radius: 0.25em;

// Box
$button--box-shadow: 0.0625em 0.125em 0.375em $color-shadow;

// Text
$button-text--color: $black;
$button-text--text-align: center;
$button-text--font-size: $font-size-small;
$button-text--line-height: 1em;
$button-text--font-size-medium: $font-size-medium;
$button-text--line-height-medium: 1.3125em;
$button-text--font-size-large: $font-size-large;
$button-text--line-height-large: 1.5em;
$button--text-decoration: none;

// Hover
$button--hover-text-decoration: underline; // Can be false

// Active
$button--active-background-color: $grey-lightest;
$button--active-border: 0.0625em solid $grey-base;
$button--active-color: $black;
$button--active-text-decoration: none; // Can be false

// Disabled
$button--disabled-background-color: $grey-lighter;
$button--disabled-border: 0em;
$button--disabled-color: $grey-base;
$button--disabled-text-decoration: none; // Can be false

// Primary
$button--primary-background-color: $vg-color-light-3; //$color-primary is default
$button--primary-color: #000000;
$button--primary-hover-text-decoration: underline; // Can be false
$button--primary-active-background-color: $white;
$button--primary-active-border: 0.0625em solid $vg-color-light-3;
$button--primary-active-color: $vg-color-light-3;
$button--primary-active-text-decoration: none; // Can be false

// Secondary
$button--secondary-background-color: $vg-color-light-1;
$button--secondary-color: $white;
$button--secondary-hover-text-decoration: underline; // Can be false
$button--secondary-active-background-color: $white;
$button--secondary-active-border: 0.0625em solid $vg-color-light-1;
$button--secondary-active-color: $vg-color-light-1;
$button--secondary-active-text-decoration: none; // Can be false

// Icon
$button--icon: "\e844";
$button--icon-margin-left: 1em;
$button--icon-text-decoration: none;
$button--icon-background-color: $vg-color-light-3;
$button--icon-color: false;

/* ************************* */
/* Atoms - Form control text */
/* ************************* */

// Font
$form-control--font-size: .875em;
$form-text--muted--font-size: .875em;

// Text
$form-text--required: $warning;

/* ************ */
/*  */
/* ************ */

// Color
$link--color: false;

// Text
$link--text-decoration: none;
$link--active-hover-text-decoration: underline;

/* ******************** */
/* Atoms - Round button */
/* ******************** */

// Background
$round-button--background: $vg-color-light-2; //-> Changed

// Border
$round-button--border: 0.0625em solid $vg-color-light-1; //-> Changed
$round-button--border-radius: 50%;

// Padding
$round-button--padding: 0.375em; //em(6);
$round-button--padding-small: 0.25em; //em(4);

// Color
$round-button--color: #000000;
$round-button--background-color-hover-active: false; //-> Changed
$round-button--color-hover-active: false; //-> Changed

// Font
$round-button--font-family: "feather"; //-> Changed
$round-button--font-size: 1em; //-> Changed
$round-button--text-decoration: none;

/* ***************** */
/* Molecules - Block */
/* ***************** */

// Width
$block--width: 100%;

// Text
$block--text-align: left;
$block__text-container--padding: 0.9375em;
$block__text-title--color: $vg-color-main;
$block__text-title--font-weight: bold;

// Image
$block__img--width: 100%;
$block__img--margin-bottom: -0.625em; //-10px

// Box
$block__box--max-width: false; //26em; //416px
$block__box--border-top: 0.0625em solid $grey-lighter;
$block__box--border-right: 0.0625em solid $grey-lighter;
$block__box--border-bottom: 0.25em solid $vg-color-main;
$block__box--border-left: 0.0625em solid $grey-lighter;
$block__box--border-radius: 0.375em;

/* ************************* */
/* Molecules - Cookie notice */
/* ************************* */

// Background
$cookie-notice--background-color: false;
$cookie-notice--background: transparent;

// Border
$cookie-notice--border: false; //1px solid #000000

// Padding
$cookie-notice--padding-top: 1.25em;
$cookie-notice--padding-right: false;
$cookie-notice--padding-bottom: 1.25em;
$cookie-notice--padding-left: false;
$cookie-notice--padding-medium-top: 0.625em;
$cookie-notice--padding-medium-bottom: 0.625em;

// Description
$cookie-notice__description--margin-top: 0.9375em;
$cookie-notice__description--margin-right: 0;
$cookie-notice__description--margin-bottom: 0.9375em;
$cookie-notice__description--margin-medium-top: 0;
$cookie-notice__description--margin-medium-right: 2.5em;
$cookie-notice__description--margin-medium-bottom: 0;

// Icon
$cookie-notice__icon: "\e81c";
$cookie-notice__icon--margin-medium: 1.25em 1.25em 1.25em 0;
$cookie-notice__icon--padding: 0;
$cookie-notice__icon--color: false; //$black;
$cookie-notice__icon--font-size: $p-small;
$cookie-notice__icon--font-size-medium: $h4-medium;
$cookie-notice__icon--font-size-large: $h2-large;
$cookie-notice__icon--font-weight: normal;

// Button
$cookie-notice__button--min-width: 13ex;

/* *********************** */
/* Organism - Footer group */
/* *********************** */

// Container
$footer-group__container--background-color: false; //Can use "false"
$footer-group-main-color: #ffffff; //Can use "false"

// Text
$footer-group__text--color: $footer-group-main-color;//Can use "false"
$footer-group__text--line-height: 1.6;

// Link
$footer-group__link--color: $footer-group-main-color;//Can use "false"
$footer-group__link--line-height: 2.75; //44px

// Header
$footer-group__header--margin-bottom: .8em;
$footer-group__header--border-bottom-width: 0.125em;
$footer-group__header--border-bottom-style: solid;
$footer-group__header--border-bottom-color: $footer-group-main-color;//Can use "false"
$footer-group__header-title--font-size: 1em;
$footer-group__header-title--font-size-large: 1.4em;
$footer-group__header-title--font-weight: bold;
$footer-group__header-title--line-height: 1.6;

// Body
$footer-group__body--font-size: 0.875em; //14px
$footer-group__body--font-size-large: 1em; //16px
$footer-group__body--word-break: break-word;
$footer-group__body-address--margin: .8em 0;

// Container
$footer-group__container--padding-y: 2.5em; //40px
$footer-group__item-container--padding-top: 0;
$footer-group__item-container--padding-right: 0;
$footer-group__item-container--padding-bottom: 2.5em;
$footer-group__item-container--padding-left: 0;
$footer-group__item-container--padding-medium-top: 1.5em;
$footer-group__item-container--padding-medium-right: 1.5em;
$footer-group__item-container--padding-medium-bottom: 1.5em;
$footer-group__item-container--padding-medium-left: 1.5em;

// Item
$footer-group__item--height: 100%;

/* ********************************* */
/* Organism - Navigation block group */
/* ********************************* */

// Container
$navigation-block-group__container--background-color: false; //-> Changed
$navigation-block-group__container--padding-y: 1.875em;  //30px

// Block
$navigation-block-group__item-container--padding-top: 0;
$navigation-block-group__item-container--padding-right: 0;
$navigation-block-group__item-container--padding-bottom: 1.2em;
$navigation-block-group__item-container--padding-left: 0;
$navigation-block-group__item-container--padding-medium-top: .6em;
$navigation-block-group__item-container--padding-medium-right: .6em;
$navigation-block-group__item-container--padding-medium-bottom: .6em;
$navigation-block-group__item-container--padding-medium-left: .6em;
$navigation-block-group__item--height: 100%;

/* ************** */
/* Atoms - Labels */
/* ************** */

// Background color
$labels--background-color: $base3-light;

// Text color
$labels--text-color: $black;

/* *************************** */
/* Atoms - Pagination elements */
/* *************************** */

// Link
$pagination-link--border: 0.046875em solid $grey-dark;
$pagination-link--hover-background-color: $base3;
$pagination-link--hover-text-color: #ffffff;
$pagination-link-current--background-color: #f4c870;
$pagination-link-current--text-color: #000000;
$pagination-link-current--font-weight: bold;
$pagination-link-current--background-color-hover-active: #f4c870;
$pagination-link-current--text-color-hover-active: #000000;
$pagination-link-current--font-weight-hover-active: bold;

/* ******************** */
/* Atoms - Search field */
/* ******************** */

// Basic
$search-field__box-height: 2.5em; //40px
$search-field__box-height-larger: 3.75em; //60px

// Padding
$search-field__term--padding: 0.625em;

// Border
$search-field__term--border-styling: 0.0625em solid $grey-light;
$search-field__term--border-top: $search-field__term--border-styling;
$search-field__term--border-left: $search-field__term--border-styling;
$search-field__term--border-bottom: $search-field__term--border-styling;
$search-field__term--border-right: 0;
$search-field__term--border-radius: 0.25em 0 0 0.25em;

// Color
$search-field__term--color: $grey-darker;

// Font
$search-field__term--font-size: .8125em;
$search-field__term--font-size-medium: .9375em;
$search-field__term--font-size-large: .9375em;
$search-field__term--font-family: false;

// Button
$search-field__button--padding-top: false;
$search-field__button--padding-right: false;
$search-field__button--padding-bottom: false;
$search-field__button--padding-left: false;
$search-field__button--border: 0.0625em solid $base3-dark;
$search-field__button--border-radius: 0 0.25em 0.25em 0;
$search-field__button--background: $base3;
$search-field__button--color: #ffffff;
$search-field__button--font-size: $p-small;
$search-field__button--font-weight: normal;

/* *************************** */
/* Molecules - Filter alphabet */
/* *************************** */

// Basic
$filter-alphabet--display: flex;
$filter-alphabet--flex-wrap: wrap;
$filter-alphabet--border: 0.0625em solid $grey-dark;
$filter-alphabet--border-radius: 0.25em; //4px

// Color
$filter-alphabet--background-color-active-hover: $base3;
$filter-alphabet--text-color-active-hover: #ffffff;

// Link
$filter-alphabet-link--focus--background-color: $base3-light;
$filter-alphabet-link--focus--color: black;
$filter-alphabet-link--focus--outline: none;
$filter-alphabet-link--padding-top: em(15);
$filter-alphabet-link--padding-bottom: em(15);
$filter-alphabet-link--padding-left: em(10);
$filter-alphabet-link--padding-right: em(10);
$filter-alphabet-link--text-decoration: none;
$filter-alphabet-link--inactive--background-color: $white;
$filter-alphabet-link--inactive--color: $grey-base;

// Medium
$filter-alphabet--medium--max-width: 843px;

// Large
$filter-alphabet--large--max-width: 898px;

/* ******************* */
/* Molecules - Message */
/* ******************* */

// Basic
$message--padding: 1.875em 0; //30px
$message--padding-medium: 1.25em 0; //20px
$message--background-color: #E3B7BE;
$message--text-align: center;
$message--line-height: 1em;

// Icon
$message__icon--content: "\e81e";
$message__icon--margin-medium: 2.5em 2.5em 2.5em 0;
$message__icon--padding: 0.5em; //8px
$message__icon--color: #700B1B;
$message__icon--text-color: #ffffff;
$message__icon--font-weight: normal;
$message__icon--font-family: "feather";
$message__icon--font-size: $p-small;
$message__icon--font-size-medium: $h4-medium;
$message__icon--font-size-large: $h2-large;

// Text
$message__text-title--color: #700B1B;

// Link
$message__link--color: $black;
$message__link--color-hover: $black;
$message__link--text-decoration: underline;
$message__link--text-decoration-hover: none;

/* ******************** */
/* vAtom - Caption Hero */
/* ******************** */

// Basic
$caption-hero--padding: .9375em;
$caption-hero--background-color: $black;
$caption-hero--color: $white;
$caption-hero--font-size: $h2-medium;

// Rounded
$caption-hero--rounded--border-radius--default: 0;
$caption-hero--rounded--border-radius--medium: 0.4ex;

// Secondary
$caption-hero--secondary-background-color: $white;
$caption-hero--secondary-color: $black;

// Medium
$caption-hero--medium--bottom: 1em; //em(16);
$caption-hero--medium--left: 2.5em; //em(40);
$caption-hero--medium--max-width: 20.385em; //530px;

// Large
$caption-hero--large--bottom: 1.5625em; //em(25);
$caption-hero--large--left: 3.75em; //em(60);
$caption-hero--large--font-size: $h2-medium;

/* ********************** */
/* vMolecule - Image Hero */
/* ********************** */

// Box-modell/beteende
$hero--width: 100%;
$hero--max-width: 90.0625em; //1441px;
$hero--min-width: 18em; //288px - iPhone 5S (320px - 20em)

// Medium
$hero--medium--max-height: 18.75em; //300px;

// Large
$hero--large--max-height: 26.25em; //420px;

// hero__image-container
$hero__image-container--max-height: 25em; //400px
$hero__image-container--width: 100%;

// hero__image
$hero__image--width: 100%;

/* ********************** */
/* Organism - Breadcrumbs */
/* ********************** */

// Border
$breadcrums--border-top: 0.0625em solid $grey-lighter;//Can using "false"
$breadcrums--border-bottom: 0.0625em solid $grey-lighter;//Can using "false"

// Link
$breadcrumbs__item-link--margin-left: false; //0.75em is default - Can using "false"
$breadcrumbs__item-link--margin-right: false; //Can using "false"
$breadcrumbs__item--border-bottom: 0.1875em solid $base3-dark ;

// Margin
$breadcrumbs__icon--margin-left: 0.75em; //Can using "false"
$breadcrumbs__icon--margin-right: 0.75em; //Can using "false"

/* ********************* */
/* Atom - Circle button */
/* ********************* */

// Basic
$circle-button--background: $base3; //false
$circle-button--font-family: "feather";
$circle-button--text-color: #ffffff; //false
$circle-button--border: 0.0625em solid $base3-dark;

// Sizes
$circle-button--small-size: 1.875em; //30px
$circle-button--medium-size: 2.1875em; //35px
$circle-button--large-size: 2.5em; //40px
$circle-button--large-icon-font-size: 1.2em;
$circle-button--xlarge-size: 3.125em; //50px
$circle-button--xlarge-icon-font-size: 1.6em;

// Effects
$circle-button--background-color-hover: $base3-light; //false
$circle-button--text-color-hover: $base3-dark; //false
$circle-button--background-color-active: $base3-light; //false
$circle-button--text-color-active: $base3-dark; //false

// Cursor
$circle-button--cursor: false; //Ex: pointer

// Secondary style
$circle-button--background-secondary: $base3-light;
$circle-button--text-color-secondary: #000000; //false
$circle-button--background-color-hover-secondary: $base3; //false
$circle-button--text-color-hover-secondary: #ffffff; //false

/* *********************** */
/* Molecules - Back to top */
/* *********************** */

// Basic
$back-to-top--isResponsive: true;
$back-to-top--background-color: $base3;
$back-to-top--z-index: 10;

// Effects
$back-to-top--background-color-hover: $base3-dark; //false
$back-to-top--text-color-hover: #ffffff; //false

// Active
$back-to-top--background-color-active: $base3-dark; //false
$back-to-top--text-color-active: #ffffff; //false

// Cursor
$back-to-top--cursor: pointer;

// Button
$back-to-top--button-size-medium: 3.125em; //50px

// Icon
$back-to-top--icon-size-medium: 2em;

// Container
$back-to-top__container--padding: .8em;